<template>
  <div class="xzxc-login">
  	<el-dialog title="提示" :close-on-click-modal="false" :visible.sync="centerDialogVisible" :custom-class="loginModal" :center="'center'" :title="'登录星知心晨后台管理'" :close-on-press-escape="false" :show-close="false" :modal="false" width="26%" center>
  		<el-form status-icon ref="ruleForm" :model="user" :rules="rules">
			<el-form-item label="用户名" type="text" :label-width="user.labelWidth" prop="userName">
				<el-input v-model="user.userName" :placeholder="user.placeholderUserName"></el-input>
			</el-form-item>
			<el-form-item label="密码" :label-width="user.labelWidth" prop="passWord">
				<el-input v-model="user.passWord" type="password" :placeholder="user.placeholderUserPassword" auto-complete="off"></el-input>
			</el-form-item>
		</el-form>
	  
	  <span slot="footer" class="dialog-footer">
	    <el-button type="primary" @click="login">登 录</el-button>
	  </span>
	</el-dialog>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  data () {
    return {
        centerDialogVisible: true,
        loginModal: 'loginModal',
        user: {
        	labelWidth: '80px',
			placeholderUserName: '请输入用户名',
			placeholderUserPassword: '请输入用户密码',
			userName: '',
			passWord: ''
	    },
	    rules: {
			userName: [
				{required: true,message: '请输入用户名'}
			],
			passWord: [
				{required: true, message: '请输入密码'}
			]
		}
    }
  },
  computed:{
  	...mapState({
      loading: 'loading'
    })
  },
  created () {
  	console.log(this.api)
  },
  methods: {
  	submitLogin () {
  		var _this = this
  		this.$axios({
	         type: '$axios',
	         obj: {
	          url: _this.api.adminlogin,
	          method: 'post',
	          success (res) {
	            if (res.data.code == 200) {
	              _this.alertsu('登录成功！')
	              window.location.href = _this.$store.state.systemPage + '/admin'
	            } else {
	              _this.alerter('登录失败！')
	            }
	          },
	          error () {
	            _this.alertsu('登录失败！')
	          },
	          data: _this.user,
	          header: {
	            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
	            'content-type': 'application/json; charset=utf-8'
	          }
	        }
	    })
  	},
  	login () {
		var _this = this
		_this.$refs.ruleForm.validate((valid) => {
			if (valid) {
			  _this.submitLogin()
			} else {
				return false
			}
		})
      
  	},
  	...mapMutations({
	     showLoading: 'showLoading'
	})
  }
}
</script>
<style lang="less">
.loginModal{
	border-radius:10px;
}
.xzxc-login .loginModal .el-dialog__title{
	font-weight: bold;
    color: #44668e;
}
</style>



